body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App {
  text-align: center;
}

body {
  padding: 0;
  margin: 0;
}
.home {
    padding-top: max(8vh, 80px);
}


/* https://www.canva.com/colors/color-palettes/mermaid-lagoon/ */
.forex_main {
    padding: 0;
    margin: 0;
    position: relative;
    display: flex;
    flex-direction: column;
    
}

.forex_title {
    font-size: 24px;
    font-weight: 300;
    width: 100vw;
    height: 60px;
    background-color: #404040;
    color: white;
    display: flex;
    align-items: center;
    justify-content: left;
    text-indent: 12vw;
    top: 0;
    left: 0;
    /* color: black; */
}

.forex_content {
    margin-left: 10vw;
    padding-top: 20px;
    width: 95vw;
}

/* .current {
    margin: 20px;
} */

.section_title {
    color: gray;
    font-size: 16px;
    margin: 16px;
    display: flex;
    padding: 0;
    text-indent: 0;
}

.blocks {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 0;
    justify-content: flex-start;
    position: relative;
}

.block {
    background-color: #81838520;
    width: 140px;
    height: 100px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 5px;
    border-radius: 5px;
    
}

.block_title {
    font-size: 12px;
    color: #818385;
    font-weight: 600;
}

.block_main_text {
    font-size: 18px;
    font-weight: 600;
    margin-top: 5px;
    margin-bottom: 5px;
    
}

.block_date {
    font-size: 10px;
    color: #818385;
}
